import {
	AfterViewInit,
	Component,
	OnDestroy,
	OnInit,
	ViewChild
} from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { HttpClient } from '@angular/common/http';
import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
import { TranslateService } from '@ngx-translate/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { DatePipe } from '@angular/common';
import { AuthenticationService } from '../../../../../core/services/authentication.service';
import {
	addMonths,
    subMonths,
    format,
    startOfMonth,
    endOfMonth,
    startOfDay,
    parse,
    getDay,
    getDate,
    startOfWeek
} from 'date-fns';
import { DatatablesResponse } from '../../../../../core/helpers/datatables-response.helper';
import { environment } from '../../../../../../environments/environment';
import { defineLocale } from 'ngx-bootstrap/chronos';
import { viLocale } from 'ngx-bootstrap/locale';
defineLocale('vi', viLocale);

import 'datatables.net';
import 'datatables.net-bs4';

@Component({
	selector: 'app-payment-reports',
	templateUrl: './payment-reports.component.html',
	styleUrls: ['./payment-reports.component.scss']
})

export class PaymentReportsComponent implements OnInit {
	private apiUrl = environment.apiUrl;
	@ViewChild(DataTableDirective, { static: true })
	dtElement: DataTableDirective;
	dtTrigger: Subject<any> = new Subject();
	dtOptions: any = {};
	loginUser: any;
    currentMonth:any = this.datePipe.transform(new Date(), 'yyyy-MM');
    month:any = this.datePipe.transform(new Date(), 'yyyy-MM');
	defects = [];
	leaves = [];
    payments = [];
	holidays = [];
	workingInMonth=0;
    exportTableData = [];
    daysInMonth: any = [{}];
	isPageLoaded = false;
	exportAsConfig: ExportAsConfig = {
		type: 'csv',
		elementIdOrContent: 'export_table',
	};
	FilterKey = 'success';
    datepickerConfig = {
        dateInputFormat: 'YYYY-MM',
        containerClass: 'theme-red',
        minMode: 'month',
        maxDate: new Date(),
        locale: 'vi',
    };

	constructor(
		public translate: TranslateService,
		private http: HttpClient,
        private datePipe: DatePipe,
		private authenticationService: AuthenticationService,
        private sanitizer: DomSanitizer,
		private exportAsService: ExportAsService
	) {
		this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
	}

	ngOnInit() {
		this.loadDatatable();
	}
	
	filterByStatus(key){
        this.FilterKey = key;
		this.loadDatatable();
    }
	loadDatatable() {
        this.http.post<DatatablesResponse>(this.apiUrl + '/api/todos/list-report', {month:this.month, module_id:'all', 'filterKey':this.FilterKey}, {})
        .subscribe(resp => {
            this.payments = resp.data;
            this.isPageLoaded = true
        });

        return;
	}
    nextMonth(){
        this.changeMonth(addMonths(this.month, 1));
    }
    preMonth(){
        this.changeMonth(subMonths(this.month, 1));
    }

    changeMonth(selectedDate: Date){
        this.month = this.datePipe.transform(selectedDate, 'yyyy-MM');
        this.loadDatatable();
    }

	exportFiles(){
        this.http.post(this.apiUrl + '/api/defect/export-payment', { data: this.payments, month:this.month, action:'payment', filterKey:this.FilterKey }, { responseType: 'blob' })
          .subscribe((blob: Blob) => {
            const link = document.createElement('a');
            const url = window.URL.createObjectURL(blob);
            link.href = url;
            link.download = 'payment_'+this.month+'.xlsx';
            link.click();
            window.URL.revokeObjectURL(url);
          });
    }

	ngOnDestroy(): void {
		this.dtTrigger.unsubscribe();
	}

	rerender(): void {
		this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
			dtInstance.destroy();
			setTimeout(() => {
				this.dtTrigger.next();

				if(this.exportTableData.length > 0) {
					$('.tfoot_dt').addClass('d-none');
				} else {
					$('.tfoot_dt').removeClass('d-none');
				}
			});
		});
	}
}
